body {
  --active: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.container {
  width: 1200px;
  margin: auto;
  padding: 0 50px;

  @media screen and (width >= 1024px) {
    & {
      max-width: 924px;
    }
  }

  @media screen and (width >= 1200px) {
    & {
      max-width: 1100px;
    }
  }
}

.logo {
  height: 40px;
  margin: 0;
}

.nav__list {
  margin: 0 -20px 0 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  align-items: center;
  margin-right: 20px;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  text-align: left;
  color: #000;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--active);
}

.is-active:after {
  content: "";
  background-color: var(--active);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
